.card {
  margin: 2%;
  border-radius: 12px;
  border: 3px solid #424242;
  background-color: #6e6e6e;
  color: gold;
  transition: all 1s;
  -webkit-transition: all 1s;
}
.card:hover {
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  box-shadow: 5px 5px #2e2e2e;
  z-index: 999;
  transform: scale(1.05) translateX(-5px);
  transition: all 1s;
  -webkit-transition: all 1s;
}

.ant-card-head {
  border-bottom: 3px solid #424242;
}
.ant-card-head-title {
  font-style: italic;
  color: gold;
  text-align: center;
}

.ant-card-body {
  text-align: center;
}

.card__title {
  display: block;
  text-align: center;
  margin: 5% 0;
}

.card-div {
  display: flex;
  justify-content: space-around;
  border-radius: 12px;
  border: 5px solid #424242;
  padding: 5%;
}

.card__image {
  width: 220px;
}

.card__type, .ant-tag {
  margin: 0;
}

.card__abilities {
  list-style-type: none;
  margin: 0;
  padding: 0;
  opacity: 0.8;
}

.card-height-weight, .card-total-average {
  display: flex;
  justify-content: space-around;
  padding-top: 5%;
}

.card__height, .card__weight, .card__total, .card__average {
  text-align: center;
}

